import React, { useEffect, useState } from "react";
import { Table, Input, Button } from "antd";
import AssignHotlineModal from "../modal/AssignHotlineModal";
import { getManageEmpAsync } from '../../actions/manageEmp';
import {Link} from "react-router-dom"

import {connect} from "react-redux";
import '../table/table.scss';


class CreateHotlineStepTwo extends React.Component{
  constructor(props){
    super(props);
    this.state={
      employeeData:"",
      selectedRowKeys: [],
      selectedRow:"",
      loading: false,
      showAssigntoModal:"false",
      checkedValue:""
    }
} 

 componentDidMount(){
  const { dispatch } = this.props;
  dispatch(getManageEmpAsync())
 }

 componentDidUpdate(prevProps){
  let { manageEmpData } = this.props;
  console.log("manageEmpData",manageEmpData);
  if(prevProps.manageEmpData != manageEmpData){
        console.log("manageEmpData", manageEmpData)
        this.setState({
          employeeData:manageEmpData
        })
    }
}

onSelectChange = (selectedRowKeys,selectedRows) => {
  console.log('selectedRowKeys changed: ', selectedRowKeys);
  console.log("selectedows", selectedRows);
  this.setState({ selectedRowKeys:selectedRowKeys, selectedRow:selectedRows});
};

start = () => {
  this.setState({
    showAssigntoModal:true
  })
  // this.setState({ loading: true });
  // // ajax request after empty completing
  // setTimeout(() => {
  //   this.setState({
  //     selectedRowKeys: [],
  //     loading: false,
  //   });
  // }, 1000);
};
  

  stepTwoPrev = () => {
    this.props.prev();
  }

  stepTwoNext = () => {
    console.log("state", this.state.selectedRow)
    this.props.getStepTwoValue(this.state.selectedRow,this.state.checkedValue);
    this.props.next();
  }

  getCheckValues=(values)=>{
    console.log("checckkk", values);
    this.setState({
      checkedValue:values
    })
  }

  render(){
    const { loading, selectedRowKeys } = this.state;
    const rowSelection = {
      selectedRowKeys,
      onChange: this.onSelectChange,
    };
    const hasSelected = selectedRowKeys.length > 0;

    const columns = [
      { title: "First Name", dataIndex: "firstname", key: "firstname", width: "22%" },
      { title: "Last Name", dataIndex: "lastname", key: "lastname", width: "22%" },
      { title: "Mobile Number", dataIndex: "Mobile_Number", key: "Mobile_Number", width: "21%" },
      { title: "Access", dataIndex: "", key: "", width: "20%" },
      {
        title: "Status",
        dataIndex: "",
        key: "x",
        width: "10%",
        // render: () => (
        //   <div className="primary-tag-rounded tag">
        //     <span className="circle mr-1"></span>Active
        //   </div>
        // ),
        render: () => <div className="primary-tag-rounded tag"><span className="circle mr-1"></span>Active</div>,
      }
    ];
    return (
      <div>
  
        <AssignHotlineModal
        getCheckValues={this.getCheckValues}
        />
        <Table
          rowSelection={rowSelection}
          columns={columns}
          dataSource={this.state.employeeData}
          rowKey="unique_identifier"
        />
        <div className="steps-action mt-5">
          <Button
            className="mr-4"
            type="secondary"
            style={{ margin: "0 8px" }}
            onClick={() => this.stepTwoPrev()}>
            Back
          </Button>
          <Button
            className="md"
            type="primary"
            onClick={() => this.stepTwoNext()}
          >
            Next
          </Button>
        </div>
      </div>
    );
  }
}

function mapStateToProps(state) {
  const { manageEmpData } = state.manageEmp;
  const { message } = state.message;
  return {
    manageEmpData,
    message
  };
}
export default connect(mapStateToProps)(CreateHotlineStepTwo);
