import React, { useState } from "react";
import { Table, Input, Col, Checkbox } from "antd";

import "./table.scss";

const { Search } = Input;

const onSearch = (value) => console.log(value);

function handleChange(value) {
  console.log(`selected ${value}`);
}

const columns = [
  { title: "", dataIndex: "name", key: "name", width: "30%" },
  {
    title: "",
    dataIndex: "",
    key: "x",
    width: "40%",
    render: () =>      
    <Col span={12} className="d-flex justify-content-end">
    <Checkbox value="reply"></Checkbox>
  </Col>,
  },
  {
    title: "",
    dataIndex: "",
    key: "x",
    width: "20%",
    render: () =>      
    <Col span={12} className="d-flex justify-content-end">
    <Checkbox value="reports"></Checkbox>
  </Col>,
  },
  {
    title: "",
    dataIndex: "",
    key: "x",
    width: "20%",
    render: () =>      
    <span className="uploaded-icon icon icon-close d-inline-flex justify-content-end align-items-center"></span>
  },
];

const data = [
  {
    key: 1,
    name: "Joe Min",

  },
  {
    key: 2,
    name: "Joh Threfol",

  },

];

const GridPermission = () => {
  return (
    <div className="no-bordered-grid">

      <Table
      className="no-bg-table"
        columns={columns}
        dataSource={data}
      />
    </div>
  );
};

export default GridPermission;
