import React from "react";
import { Layout, Row, Col, Input } from "antd";
import { LoggedinHeader } from "../../containers";
import "../manageOrg/manageOrg.scss";
import Sidebar from "./Sidebar";
import Grid from "../table/Table";
import Buttons from "../button/Button";
import { AudioOutlined } from "@ant-design/icons";
import { connect } from "react-redux";
import { getManageOrgAsync } from '../../actions/manageOrg';
import Pencil from "../../assets/icons/pencil.svg"

const { Search } = Input;

const suffix = (
  <AudioOutlined
    style={{
      fontSize: 16,
      color: "#1890ff",
    }}
  />
);

const { Content } = Layout;
class ManageOrg extends React.Component {

  constructor(props) {
    super(props);

    const columns = [
      { title: "Organization Name ", dataIndex: "name", key: "name", width: "22%" },
      { title: "Organization id", dataIndex: "identifier", key: "identifier", width: "22%" },
      { title: "Start Date ", dataIndex: "start_date", key: "start_date", width: "21%" },
      { title: "End Date", dataIndex: "end_date", key: "end_date", width: "20%" },
      {
        title: "Status",
        dataIndex: "",
        key: "x",
        width: "10%",
        render: () => (
          <div className="primary-tag-rounded tag">
            <span className="circle mr-1"></span>Active
          </div>
        ),
        // render: () => <div className="secondary-tag-rounded tag">Inactive</div>,
      },
      {
        title: "",
        dataIndex: "",
        key: "x1",
        width: "5%",
        render: () => <img src={Pencil} alt="Edit" />,
      },
    ];
    this.state = {
      columns: columns,
      data: []

    }
  }

  componentDidMount() {
    const { dispatch } = this.props;
    dispatch(getManageOrgAsync())
  }

  UNSAFE_componentWillReceiveProps(props) {

    let { manageOrgData } = props;
    manageOrgData.forEach(org => {
      org.start_date = org.start_date.replaceAll('-', ' / ');
      org.end_date = org.end_date.replaceAll('-', ' / ');
    });
    this.setState({ data: manageOrgData }, () => { this.onSearch('') });
  }

  filterIt(arr, searchKey) {
    if (!searchKey || searchKey === '' || searchKey === null) {
      return arr;
    }
    return arr.filter(function (obj) {
      return Object.keys(obj).some(function (key) {
        if (typeof obj[key] === 'string') {
          return obj[key].includes(searchKey);
        }
      })
    });
  }

  onSearch = (value) => {
    const { data } = this.state;
    const filterDt = this.filterIt(data, value);
    this.setState({ filterData: filterDt });

  }


  render() {
    return (
      <>
        <Row className="inner-header">
          <LoggedinHeader />
        </Row>
        <Row className="content-center pt-0">
          <Layout>
            <Layout>
              <Sidebar />
              <Content className="">
                <Row className="py-3">
                  <h1>Manage Organizations </h1>
                </Row>
                <Col span={24}>
                  <Row className="pt-1 pb-3">
                    <Col span={18} className="search-row">
                      <Row>
                        <Col span={9} className="search-box">
                          <Search placeholder="Search" onSearch={this.onSearch} />
                        </Col>
                      </Row>
                    </Col>

                    <Col
                      span={6}
                      className="d-flex align-items-center justify-content-end"
                    >
                      <Buttons value="Add Organization" />
                    </Col>
                  </Row>
                </Col>
                <Grid key='manageOrg' rowKey="identifier" columns={this.state.columns} data={this.state.filterData} />
              </Content>
            </Layout>
          </Layout>
        </Row>
      </>
    );
  };

}


function mapStateToProps(state) {
  const { manageOrgData } = state.manageOrg;
  const { message } = state.message;
  return {
    manageOrgData,
    message
  };
}

export default connect(mapStateToProps)(ManageOrg);