import React, { Component } from "react";
import { Form, Input, Button, Checkbox, Row, Col } from "antd";
import { CommonHeader } from "../../containers";
import Amccvector from "../../assets/images/amcc-vector.svg";
import "./commonLogin.scss";
import { connect } from "react-redux";
import { login } from "../../actions/auth";
import { Redirect } from "react-router-dom";

const layout = {
  wrapperCol: {
    span: 24,
  },
};
const tailLayout = {
  wrapperCol: {
    span: 24,
  },
};

class Login extends Component {
  constructor(props) {
    super(props);
    this.handleLogin = this.handleLogin.bind(this);
    this.onChangeUsername = this.onChangeUsername.bind(this);
    this.onChangePassword = this.onChangePassword.bind(this);
    this.state = {
      email: "",
      password: "",
    };
  }
  onChangeUsername(e) {
    this.setState({
      email: e.target.value,
    });
  }
  onChangePassword(e) {
    this.setState({
      password: e.target.value,
    });
  }

  handleLogin() {
    this.setState({
      loading: true,
    });
    const { dispatch, history } = this.props;

    dispatch(login(this.state.email, this.state.password))
      .then(() => {
        history.push("/organization");
        window.location.reload();
      })
      .catch(() => {
        this.setState({
          loading: false,
        });
      });
  }
  canSubmit() {
    const { email, password } = this.state;
    return email.length > 0 && password.length > 0;
  }

  render() {
    const isEnabled = this.canSubmit();
    const { isLoggedIn, message } = this.props;
    if (isLoggedIn) {
      return <Redirect to="/organization" />;
    }

    return (
      
      <>
        <Row className="px-4 py-3">
          <CommonHeader />
        </Row>
        <Row className="content-center mt-1 px-4">

          <Col span={12} className="vector-area justify-content-center mt-1 d-none d-lg-block">
            <Col span={24} className="text-center">
              <h5 className="text-center primary-text">Control Center and Insights Dashboard</h5>
            </Col>
            <Row className="justify-content-center mt-3">
              <img src={Amccvector} alt="Annonymoose" className="pr-4" />
            </Row>
            <Row className="pt-3">
              <p className="w-100 text-center text-wgt-md">
                Raising Voice Anonymously Made Easy
            </p>
            </Row>

          </Col>
          <Col lg={{ span: 12 }} md={{ span: 24 }} xs={{ span: 24 }} className="d-flex justify-content-center align-items-center">

            <div className=" col-md-24 col-lg-12 col-xl-10 login-box justify-content-center d-flex">
              <Row className="mb-4">
                <h2>Sign In</h2>
              </Row>

              <Form
                {...layout}
                onSubmit={this.handleLogin}
                ref={(c) => {
                  this.form = c;
                }}
                name="basic"
                initialValues={{
                  remember: true,
                }}

              >

                <Form.Item className={"group-floating-label"}

                  name="email"
                  rules={[
                    {
                      required: true,
                      message: 'Please enter your email!',
                    },
                  ]}
                >
                  <Input type ="email" className="input-control" placeholder=" " value={this.state.email} onChange={this.onChangeUsername} suffix={<label className="floating-label" htmlFor="name">Email</label>} />

                </Form.Item>

                <Form.Item className={"group-floating-label"}
                  name="password"
                  rules={[
                    {
                      required: true,
                      message: 'Please enter your password!',
                    },
                  ]}
                >
                  <Input type="password" className="input-control" value={this.state.password} onChange={this.onChangePassword} placeholder=" " suffix={<label className="floating-label" htmlFor="name">Password</label>} />
                </Form.Item>
                <div className="forgot-pwd">
                  <a href="#">
                    Forgot Password
            </a>
                </div>

                <Form.Item {...tailLayout}>
                  <Button disabled={!isEnabled} type="primary" htmlType="submit" className="primary-btn-lg w-100 mt-4" onClick={() => { this.handleLogin() }}>
                    SIGN IN
            </Button>
                </Form.Item>
              </Form>
            </div>
          </Col>
        </Row>
      </>
    );
  }
}

function mapStateToProps(state) {
  const { isLoggedIn } = state.auth;
  const { message } = state.message;
  return {
    isLoggedIn,
    message,
  };
}
export default connect(mapStateToProps)(Login);
