import{
    HOTLINE_FAIL,
    HOTLINE_SUCCESS
} from "../actions/types";

const deafaultState={};
deafaultState.hotlinesdata=[];

export default function(state=deafaultState,action){
    const {type,payload}=action;
    
    switch(type){
        case HOTLINE_SUCCESS:
            return {
                ...state,
                hotlinesdata:payload.hotlinesdata,
            };
        case HOTLINE_FAIL:
            return{
                ...state,
                hotlinesdata:[],
            };
        default:
            return state;
    }
}

