import React from "react";
import { Avatar } from "antd";
import "../userInfo/userInfo.scss";
import { Popover, List } from "antd";
import ExpandMore from '../../assets/icons/expand-more.svg';
import AuthService from './../../services/auth-service';


class AnonymooseUser extends React.Component {
  constructor(props) {
    super(props);
    this.handleLogoutClick = this.handleLogoutClick.bind(this);
    this.state = { isLoggedIn: false };
  }

  handleLogoutClick() {
    AuthService.logout();
  }

  render() {
    const commonForAllUsers = JSON.parse(localStorage.getItem("user"));
    const email = commonForAllUsers.user_data.email;
    const isamcc = commonForAllUsers.user_data.is_amcc_user;
    const isorgcc = commonForAllUsers.user_data.is_org_cc_user;
    const name = commonForAllUsers.user_details.first_name + " " + commonForAllUsers.user_details.last_name;
    const nameCapitalized = name.charAt(0).toUpperCase() + name.slice(1);
    const fullName = nameCapitalized.split(' ');
    const initials = fullName.shift().charAt(0) + fullName.pop().charAt(0);
    let userType = "";

    if (isamcc == true) {
      userType = "AM CC";
    }
    else if (isorgcc == true) {
      userType = "Org Control Center";
    }
    return (
      <>
        <Popover
          placement="bottom"
          content=
          {
            <List
              size="large"
              bordered>
              <List.Item>
                <div className="email">{email}<br />
                  {/* {this.showLoggedinUserData()} */}
                  <a href="/">Edit Profile</a>
                </div>
              </List.Item>
              <List.Item>
                Change Password
            </List.Item>
              <List.Item onClick={this.handleLogoutClick}>
                Sign Out
            </List.Item>
            </List>}
          trigger="click"
        >
          <Avatar className="mr-2">{initials}</Avatar>
          <span className="d-flex flex-column">
            <span className="uname-top">{nameCapitalized}</span>
            <span className="u-type">{userType}</span>
          </span>
          <span className="expand-arrow"><img src={ExpandMore} alt="Expand Arrow" className="pr-4" /></span>
        </Popover>
      </>
    );
  }
}

export default AnonymooseUser;
