import{
    MANAGEORG_FAIL,
    MANAGEORG_SUCCESS
} from "../actions/types";

const deafaultState={};
deafaultState.manageOrgData=[];

export default function(state=deafaultState,action){
    const {type,payload}=action;
    
    switch(type){
        case MANAGEORG_SUCCESS:
            return {
                ...state,
                manageOrgData:payload.manageOrgData,
            };
        case MANAGEORG_FAIL:
            return{
                ...state,
                manageOrgData:[],
            };
        default:
            return state;
    }
}

