import{
    MANAGEEMP_FAIL,
    MANAGEEMP_SUCCESS
} from "../actions/types";

const deafaultState={};
deafaultState.manageEmpData=[];

export default function(state=deafaultState,action){
    const {type,payload}=action;
    
    switch(type){
        case MANAGEEMP_SUCCESS:
            return {
                ...state,
                manageEmpData:payload.manageEmpData,
            };
        case MANAGEEMP_FAIL:
            return{
                ...state,
                manageEmpData:[],
            };
        default:
            return state;
    }
}

