import React from "react";
import { Layout, Steps, Button, message, Row, Col, Breadcrumb } from "antd";
import { LoggedinHeader } from "../../containers";
import Buttons from "../button/Button";
import "../hotlineCreation/hotlineCreation.scss";
import Cform from "../form/CreateHotlineForm";
import { connect } from "react-redux";
import CreateHotlineStepOne from "../form/CreateHotlineStepOne";
import CreateHotlineStepTwo from "../form/CreateHotlineStepTwo";
import CreateHotlineStepThree from "../form/CreateHotlineStepThree";
import GridAssignEmp from "../../components/table/TableAssignEmp";
import Sidebar from "../../containers/Sidebar";
import moment from 'moment';
import authHeader from '../../services/auth-header';
import axios from "axios";
import { Link } from 'react-router-dom';


const { Content } = Layout;

const { Step } = Steps;

const steps = [
  {
    title: "Add Details",
    content: "First-content",
  },
  {
    title: "Assign Employee",
    content: "Second-content",
  },
  {
    title: "Create Directory",
    content: "Last-content",
  },
];

class HotlineCreation extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      current: 0,
      stepOneHotLine: "",
      assingUserIds: [],
      canViewReports: 0,
      canRespond: 0

    };
  }
  next = () => {
    const { current } = this.state;
    this.setState({ current: current + 1 });
  }

  prev = () => {
    const { current } = this.state;
    this.setState({ current: current - 1 });
  }

  getStepOneValue = (hotLinedata) => {
    console.log("hotlineForm", hotLinedata)
    this.setState({
      stepOneHotLine: hotLinedata
    })
  }

  getStepTwoValue = (selectedRow, checkedValues) => {
    console.log('step two', selectedRow);
    console.log('step checkedValues', checkedValues);
    if (checkedValues[0] === 'reply') {
      this.setState({
        canRespond: 1
      })
    }

    if (checkedValues[1] === 'reports') {
      this.setState({
        canViewReports: 1
      })
    }

    const newArray = selectedRow.map(element => element.user_id);
    this.setState({
      assingUserIds: newArray
    })
  }


  createHotLine = (values) => {
    const user = JSON.parse(localStorage.getItem('user'));
    var data = [];
    data.push({
      "name": values.directoryName,
      "help_text": values.description,
      "start_date": values.sDate ? moment(values.sDate).format('DD-MM-YYYY') : "",
      "end_date": values.tDate ? moment(values.tDate).format('DD-MM-YYYY') : "",
      "hotline_icon_id": parseInt(values.hotlineIconId),
      "assing_user_ids": this.state.assingUserIds,
      "can_respond": this.state.canRespond,
      "can_view_reports": this.state.canViewReports,
      "org_user_id": user.user_details.org_user_id
    })
    const token = authHeader();

    const options = {
      headers: {
        'Authorization': token,
        'Accept': 'application/json, text/plain',
        'Content-Type': 'application/json;charset=UTF-8'
      }
    };
    console.log("values final", values, "dataone", data[0]);
    axios.post('http://10.0.6.10:8000/api/V1/hotline/', data[0], options)
      .then(res => {
        console.log(res);
        console.log(res.data);
        window.location = "/displayhotlines"
      })
  }



  render() {
    const { current, stepOneHotLine } = this.state;
    return (
      <>
        <Row className="inner-header">
          <LoggedinHeader />
        </Row>
        <Row className="content-center pt-0">
          <Layout>
            <Layout>
              <Sidebar />
              <Content className="">
                <Row className="pt-3">
                  <Breadcrumb separator=">">
                    <Link to="/displayhotlines"><Breadcrumb.Item >Hotline Directory</Breadcrumb.Item></Link>
                    <Breadcrumb.Item>
                      Create Hotline Directory
                    </Breadcrumb.Item>
                  </Breadcrumb>
                </Row>
                <Row className="py-3">
                  <h1 className="mb-0">Create Hotline Directory Entries</h1>
                </Row>
                <Steps className="mt-2" current={current}>
                  {steps.map((item) => (
                    <Step key={item.title} title={item.title} />
                  ))}
                </Steps>
                <div className="mid-content mt-4">
                  <Row>
                    <Col span={24}>
                      <div className="steps-content">
                        {console.log("current", current)}
                        {current == 0 ?
                          <CreateHotlineStepOne next={this.next} getStepOneValue={this.getStepOneValue} /> : current == 1 ?
                            <CreateHotlineStepTwo next={this.next} prev={this.prev} getStepTwoValue={this.getStepTwoValue} /> : current == 2 ?
                              <CreateHotlineStepThree prev={this.prev} hotLineValues={stepOneHotLine} createNewHotLine={this.createHotLine} /> : ""}
                        {/* <Cform />
                        <Grid /> */}
                      </div>
                      {/* <div className="steps-action mt-5">
                        {current > 0 && (
                          <Button
                            className="mr-4"
                            type="secondary"
                            style={{ margin: "0 8px" }}
                            onClick={() => this.prev()}
                          >
                            Back
                          </Button>
                        )}
                        {current < steps.length - 1 && (
                          <Button
                            className="md"
                            type="primary"
                            onClick={() => this.next()}
                          >
                            Next
                          </Button>
                        )}
                        {current === steps.length - 1 && (
                          <Button
                            type="primary"
                            onClick={() => message.success("Created")}
                          >
                            Create
                          </Button>
                        )}
                      </div> */}
                    </Col>
                  </Row>
                </div>
              </Content>
            </Layout>
          </Layout>
        </Row>
      </>
    );
  }
}

export default HotlineCreation;
