import React from 'react'
import { Layout, Menu, Row } from "antd";
const { Sider } = Layout;
import {manageemp} from '../../assets/icons/Icon';

const Sidebar = () => {
  return (
    <Sider>
    <Menu theme="dark" mode="inline" defaultSelectedKeys={["1"]}>
        <Menu.Item key="1" icon={<img src={manageemp} />}> Manage Organization</Menu.Item >
    </Menu>
    <div className="btm-div">
      <a href="/" className="px-2">info</a>
      <a href="/" className="px-2">Help</a>
    </div>
  </Sider>
  )
}

export default Sidebar
