import React, { useEffect, useState } from "react";
import {
  Form,
  Select,
  Input,
  Button,
  Upload,
  Row,
  Col,
  DatePicker,
} from "antd";
import AddIcoModal from "../../components/modal/AddIcoModal";
import { connect } from "react-redux";
import "../form/form.scss";
import ReactHtmlParser from 'react-html-parser';
const { TextArea } = Input;
const { Option } = Select;

const CreateHotlineStepThree = ({ prev, hotLineValues, createNewHotLine }) => {
  const [hotlineForm, setHotlineForm] = useState({ directoryName: "", description: "", status: "", sDate: "", tDate: "", hotlineIconId: "",iconHtml:"" })
  const [iconHtml, setIconHtml] = useState('');
  const [form] = Form.useForm();
  const stepThreeBack = (e) => {
    e.preventDefault();
    prev();

  }
  const generateHotLine = (e) => {
    console.log("valuess", hotlineForm)
    createNewHotLine(hotlineForm);
  }


  const handleChange = (e) => {
    setHotlineForm({ ...hotlineForm, [e.target.id]: e.target.value })
  }
  const handleOnSelect = (value, e, name) => {
    setHotlineForm({ ...hotlineForm, [name]: value })
  }
  const handleDatePickerChange = (date, dateString, name) => {
    setHotlineForm({ ...hotlineForm, [name]: date })
  }
  useEffect(() => {
    console.log("values from step one", hotLineValues);

    setHotlineForm({
      sDate: hotLineValues.sDate,
      tDate: hotLineValues.tDate,
      iconHtml:hotLineValues.iconHtml, 
      directoryName: hotLineValues.directoryName,
      description: hotLineValues.description,
      status: hotLineValues.status,
      hotlineIconId:hotLineValues.hotlineIconId
    })

    form.setFieldsValue({
      directoryName: hotLineValues.directoryName,
      description: hotLineValues.description,
      status: hotLineValues.status,
      sDate: hotLineValues.sDate,
      tDate: hotLineValues.tDate
    })
  }, []);
  return (
    <Form className="input-md w-75" name="validate_other" form={form}  >
      <Form.Item
        name="directoryName"
      // rules={[
      //   {
      //     required: true,
      //     message: "Please enter directory name ",
      //   },
      // ]}
      >
        <TextArea
          showCount
          maxLength={80}
          value={hotlineForm.directoryName}
          //onChange={e=>handleChange(e)}
          id={'directoryName'}
          className="input-control textarea-input"
          placeholder="Directory Name"
          htmlFor="directoryName"
        />
      </Form.Item>
      <Form.Item
        name="description"
      // rules={[
      //   {
      //     required: true,
      //     message: "Please enter description",
      //   },
      // ]}
      >
        <TextArea
          className="input-control textarea-md"
          value={hotlineForm.description}
          // onChange={e=>handleChange(e)}
          id={'description'}
          placeholder="Description"
          htmlFor="description"
        />
      </Form.Item>

      <Form.Item className="input-control datepicker-wrapper">
        <DatePicker
          className="datepicker w-50 mr-5"
          name="sdate"
          value={hotlineForm.sDate}
          // onChange={(date, dateString) => handleDatePickerChange(date, dateString,'sDate')}
          rules={[
            {
              required: true,
              message: "Please select start date",
            },
          ]}
        />
        <DatePicker className="datepicker w-50"
          name="tDate"
          value={hotlineForm.tDate}
        //  onChange={(date, dateString) => handleDatePickerChange(date, dateString,'tDate')}
        />
      </Form.Item>
      <Form.Item
        name="status"
      // rules={[
      //   {
      //     required: true,
      //     message: "Please select status",
      //   },
      // ]}
      >
        <Select
          className="input-control w-50"
          placeholder="Status"
          value={hotLineValues.status}
          htmlFor="status"
        // onSelect={(value, event) =>handleOnSelect(value, event,'status')}
        >
          <Select.Option value="1">Active</Select.Option>
        </Select>
      </Form.Item>
      <Form.Item className="w-50">
        <Form.Item
          className="upload-control"
          name="upload"
          valuePropName="fileList"
        >

          <div className="ant-upload-list ant-upload-list-picture">
            <div className="ant-upload-list-item ant-upload-list-item-list-type-picture">
              <div className="ant-upload-list-item-info d-inline-flex w-100">
                <span className="d-inline-flex align-items-center">
                  <a
                    className="ant-upload-list-item-thumbnail"
                    href=""
                    target="_blank"
                    rel=""
                  >
                    {ReactHtmlParser(hotLineValues.iconHtml)}

                  </a>

                  <span
                    className="ant-upload-list-item-name ant-upload-list-item-name-icon-count-1"
                    title="women safety"
                  >
                    Women Safety
                  </span>
                  <span className="uploaded-icon icon icon-close d-inline-flex justify-content-end align-items-center"></span>
                </span>
              </div>
            </div>
          </div>
        </Form.Item>
      </Form.Item>
      <div className="steps-action mt-5">
        <Button type="secondary" className="mr-4"
          type="secondary"
          style={{ margin: "0 8px" }} htmlType="submit" onClick={stepThreeBack}>
          Back
            </Button>
        <Button type="primary" className="md"
          htmlType="submit" onClick={generateHotLine}>
          Create
            </Button>
      </div>
    </Form>
  );

}

export default CreateHotlineStepThree;
